.player-menu {
    position: fixed;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    height: 50px;

    /* Динамическая ширина с максимальным ограничением */
    width: calc(100% - 30px); /* Ширина с отступами по 15px с каждой стороны */
    max-width: 670px; /* Максимальная ширина (700px - 30px отступов) */

    background-image: url('img/ui/menubg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 

    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    border-radius: 8px;
    transition: all 0.3s ease;
}

/* Для более заметного эффекта парения при наведении */
.player-menu:hover {
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
    transform: translateX(-50%) translateY(-2px); /* Сохраняем центрирование и добавляем эффект поднятия */
}

/* Медиа-запрос для маленьких экранов (можно добавить при необходимости) */
@media (max-width: 480px) {
    .player-menu {
        height: 45px; /* Опционально: уменьшаем высоту на маленьких экранах */
        bottom: 25px; /* Опционально: уменьшаем отступ снизу */
    }
}